import React, {Component} from 'react';
import Dogslist from '../components/Dogslist';
import Searchbox from '../components/Searchbox';
import Scroll from '../components/Scroll';
import './App.css';


class App extends Component {
  constructor() {
    super()
    this.state = {
      dogs: [],
      searchfield: ''
    }
  }

  componentDidMount() {
    fetch('https://jsonplaceholder.typicode.com/users')
      .then(response=> response.json())
      .then(users => {this.setState({ dogs: users})});
  }


  onSearchChange = (event) => {
    this.setState({searchfield: event.target.value});
  }

  render () {
    const {dogs, searchfield} = this.state;
    const filterDogs = dogs.filter(dog=>{
      return dog.name.toLowerCase().includes(searchfield.toLowerCase());
    })
    return (
      <div className='tc'>
        <h1 className='f1'>Doggy Friends</h1>
        <Searchbox searchChange = {this.onSearchChange}/>
        <Scroll>
        <Dogslist dogs = {filterDogs}/>
        </Scroll>
      </div>
    );
  }
}

export default App;