import React from 'react';
import Card from './Card';
import './Dogslist.css';

const Dogslist = ({dogs}) => {
  return (
    <div className='container'>  
      {
        dogs.map((dog, i)=> {
          return (
          <Card 
          key={i} 
          id={dogs[i].id} 
          name={dogs[i].name} 
          email={dogs[i].email} 
          />
        );
      })
    }
  </div>
  );
}

export default Dogslist;

